import { UI_BUSY, UI_OVERLAY } from '@constants';

const defaultState = {
  busy: false,
  isOverlay: false
};

export default (state = defaultState, action) => {
  if (action.type === UI_BUSY) {
    // 
    return {
      ...state,
      busy: action.payload
    };
  }

  if (action.type === UI_OVERLAY) {
    //  
    return {
      ...state,
      isOverlay: action.payload
    };
  }

  return state;
};
